import React from 'react'
import sidebarcss from './sidebar.css';


const Sidebar = props =>{
let sidebar = "sidebar" ;
  if (!props.show) {
sidebar = "sidebar open" ;
} 
return (
   <div className={sidebar} >
  <div>
<p className="sidebaritems" >   </p>
<p className="sidebaritems">   </p>
<p className="sidebaritems">    </p>
<p className="sidebaritems">    </p>
<p className="sidebaritems">    </p>
<p className="sidebaritems">   </p>
  </div>
  </div>
  
  
)
} 
export default Sidebar